import React, { useEffect } from 'react';
import { fadeInSlide } from '../animations/anime-js.jsx';
import { isWebpSupported } from 'react-image-webp/dist/utils/index.js';
import swiperLayout from '../layouts/init-swiper.jsx';
import mainSlide from '../assets/main-slide.jsx';
import AudioPlayer from './AudioPlayer.jsx';
import PropTypes from 'prop-types';

export default function Mainslide({baseUrl}) {
	useEffect(() => {
		swiperLayout();
		mainSlide();
	}, []);
	useEffect(() => {
		const slideWrappers = document.querySelectorAll(
			'.main-slide__slide-wrapper');
		if (!slideWrappers.length) return; // ,  -
	                                     // 

		//        
		const firstSlideWrapper = slideWrappers[0];
		const isActive = firstSlideWrapper.classList.contains('swiper-slide-active');

		if (isActive) {
			//    ,  
			fadeInSlide();
		}

		//     -
		slideWrappers.forEach((slideWrapper) => {
			const observer = new MutationObserver((
				mutations) => {
				mutations.forEach((mutation) => {
					if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
						const isActive = slideWrapper.classList.contains('swiper-slide-active');
						if (isActive) {
							//  -   'swiper-slide-active',
							//  
							fadeInSlide();
						}
					}
				});
			});

			observer.observe(slideWrapper, {attributes: true});
			//     
			return () => {
				observer.disconnect();
			};
		});
	}, []);

	const getPath = (fileName) => {
		return `${baseUrl}/${fileName}`;
	};

	return (
		<div className="main-slide">
			<div className="main-slide__body _swiper">
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture> {
							isWebpSupported()
								? <img src={getPath('./img/main/slides/slide_1.webp')}
								       alt="slide-1"/>
								: <img src={getPath('./img/main/slides/slide_1.jpg')}
								       alt="slide-1"/>
						}
						</picture>
					</div>
					<div className="main-slide__content">
						<div className="main-slide__title el-slidetitle">
							<span> </span> 
						</div>
						<div className="main-slide__text"> -  
							,       
							    ,  
							      , 
							,   .
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? <img src={getPath('./img/main/slides/slide_4.webp')}
								       alt="slide-4"/>
								: <img src={getPath('./img/main/slides/slide_4.jpg')}
								       alt="slide-4"/>}
						</picture>
					</div>
					<div className="main-slide__content">
						<div className="main-slide__title el-slidetitle"><span>  </span>
							
						</div>
						<div className="main-slide__text">   , 
							     .   ,
							       
							  ,      .
							          .
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? <img src={getPath('./img/main/slides/slide_2.webp')}
								       alt="slide-2"/>
								: <img src={getPath('./img/main/slides/slide_2.jpg')}
								       alt="slide-2"/>}
						</picture>
					</div>
					<div className="main-slide__content">
						<div className="main-slide__title el-slidetitle"><span>    </span>
							 
						</div>
						<div className="main-slide__text">     
							  ,      
							.       , 
							 .
						</div>
					</div>
				</div>
				<div className="main-slide__slide-wrapper">
					<div className="main-slide__slide-image _img">
						<picture>
							{isWebpSupported()
								? <img src={getPath('./img/main/slides/slide_3.webp')}
								       alt="slide-3"/>
								: <img src={getPath('./img/main/slides/slide_3.jpg')}
								       alt="slide-3"/>}
						</picture>
					</div>
					<div className="main-slide__content">
						<div className="main-slide__title el-slidetitle">
							<span>   </span>  
						</div>
						<div className="main-slide__text">   
							 ,      ,   ! 
							     ,  
							      .
						</div>
					</div>
				</div>
			</div>
			<div className="main-slide__pagination"></div>
			<div className="main-slide__media">
				<AudioPlayer/>
			</div>
		</div>

	);
};

Mainslide.propTypes = {
	baseUrl: PropTypes.string.isRequired,
};